<template>
  <div class="narrow-wrapper">
    <h1>{{ $route.name }}</h1>
    <b-form @submit.prevent="onSubmit">
      <b-form-group id="input-group-1" label="Email:" label-for="input-1">
        <b-form-input
          id="input-1"
          v-model="form.email"
          type="email"
          placeholder="Enter email"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-2" label="Password:" label-for="input-2">
        <b-form-input
          id="input-2"
          v-model="form.password"
          type="password"
          placeholder="Enter password"
          required
        ></b-form-input>
      </b-form-group>

      <b-button type="submit" variant="primary">Submit</b-button>

      <p v-if="error">
        Login failed. Please make sure you are using the correct email and
        password.
      </p>
    </b-form>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      form: {
        email: "",
        password: "",
      },
      loading: false,
      error: null,
    };
  },
  methods: {
    onSubmit() {
      axios
        .post(this.$store.state.apiURL + "login.php", JSON.stringify(this.form))
        .then((response) => {
          this.$store.commit("setUser", response.data.jwt);
          let redirect = this.$route.params.redirect;
          let path = "/" + (redirect ? redirect : "dashboard");
          this.$router.push(path, () => {});
        })
        .catch((error) => {
          this.error = error;
        });
    },
  },
};
</script>
